import './ForecastSettings.scss';

export default class ForecastSettings {
  constructor(state, getCurrentState) {
    this.state = state;
    this.getCurrentState = getCurrentState;
  }

  render() {
    this.forecastSettings = document.createElement('div');
    this.forecastSettings.classList.add('forecast-controll--settings');

    this.changeImageBtn = document.createElement('button');
    this.changeImageBtn.classList.add('forecast-controll--settings-btn-changeimage');
    this.changeLangBtn = document.createElement('select');
    this.changeLangBtn.setAttribute('id', 'language');

    this.changeLangEn = document.createElement('option');
    this.changeLangRu = document.createElement('option');
    this.changeLangBy = document.createElement('option');
    this.changeLangEn.setAttribute('value', 'en');
    this.changeLangEn.text = 'EN';
    this.changeLangEn.classList.add('option');
    this.changeLangRu.setAttribute('value', 'ru');
    this.changeLangRu.text = 'RU';
    this.changeLangRu.classList.add('option');
    this.changeLangBy.setAttribute('value', 'be');
    this.changeLangBy.text = 'BY';
    this.changeLangBy.classList.add('option');

    this.changeDegreesWrapper = document.createElement('div');
    this.changeDegreesWrapper.classList.add('forecast-controll--settings-btn-wrapper');
    this.changeDegreesC = document.createElement('button');
    this.changeDegreesC.classList.add('forecast-controll--settings-btn-cel');
    this.changeDegreesCText = document.createElement('span');
    this.changeDegreesCText.classList.add('forecast-controll--settings-text-cel');
    this.changeDegreesF = document.createElement('button');
    this.changeDegreesF.classList.add('forecast-controll--settings-btn-far');
    this.changeDegreesFText = document.createElement('span');
    this.changeDegreesFText.classList.add('forecast-controll--settings-text-far');

    this.syncAltIcon = document.createElement('i');
    this.syncAltIcon.classList.add('fas');
    this.syncAltIcon.classList.add('fa-sync-alt');
    this.arrowDown = document.createElement('i');
    this.arrowDown.classList.add('fas');
    this.arrowDown.classList.add('fa-angle-down');

    this.forecastSettings.appendChild(this.changeImageBtn);
    this.forecastSettings.appendChild(this.changeLangBtn);
    this.forecastSettings.appendChild(this.changeDegreesWrapper);
    this.changeDegreesWrapper.appendChild(this.changeDegreesC);
    this.changeDegreesWrapper.appendChild(this.changeDegreesF);
    this.changeLangBtn.appendChild(this.changeLangEn);
    this.changeLangBtn.appendChild(this.changeLangRu);
    this.changeLangBtn.appendChild(this.changeLangBy);
    this.changeImageBtn.appendChild(this.syncAltIcon);
    this.changeLangBtn.appendChild(this.arrowDown);
    this.changeDegreesC.appendChild(this.changeDegreesCText);
    this.changeDegreesF.appendChild(this.changeDegreesFText);
    this.changeDegreesCText.innerHTML = '&#x2103';
    this.changeDegreesFText.innerHTML = '&#x2109';
    this.changeLangBtn.value = localStorage.getItem('language') ? localStorage.getItem('language') : 'en';
    if (localStorage.getItem('isCelcius') === 'yes' || !localStorage.getItem('isCelcius')) {
      this.changeDegreesC.classList.add('activeDegreesBtn');
    } else {
      this.changeDegreesF.classList.add('activeDegreesBtn');
    }
    return this.forecastSettings;
  }
}
