import { getPlaceholderAndSearch } from '../../../constants/constants';

export default class SettingsBarController {
  constructor(
    state,
    getCurrentState,
    setDataToDOM,
    getCountryName,
    setCurrentState,
    defineCurrentLocalTime,
    defineCurrentYearTime,
    defineDayTime,
    getCurrentAppPicture,
  ) {
    this.state = state;
    this.getCurrentState = getCurrentState;
    this.setDataToDOM = setDataToDOM;
    this.getCountryName = getCountryName;
    this.setCurrentState = setCurrentState;
    this.defineCurrentLocalTime = defineCurrentLocalTime;
    this.defineCurrentYearTime = defineCurrentYearTime;
    this.defineDayTime = defineDayTime;
    this.getCurrentAppPicture = getCurrentAppPicture;
  }

  addEventListenersHandler() {
    const cel = document.querySelector('.forecast-controll--settings-btn-cel');
    const far = document.querySelector('.forecast-controll--settings-btn-far');
    const lang = document.querySelector('#language');
    const changePicture = document.querySelector('.forecast-controll--settings-btn-changeimage');
    far.addEventListener('click', (e) => this.convertToFarenheitHandler(e));
    cel.addEventListener('click', (e) => this.convertToCelciustHandler(e));
    lang.addEventListener('change', (e) => this.changeCurrentLanguageHandler(e));
    changePicture.addEventListener('click', (e) => this.changeImageAppHandler(e));
  }

  async changeImageAppHandler() {
    const changeImage = document.querySelector('.fa-sync-alt');
    changeImage.classList.add('rotateIn');
    changeImage.setAttribute('disabled', 'true');
    const container = document.querySelector('.container');
    this.defineCurrentLocalTime();
    this.defineCurrentYearTime();
    this.defineDayTime();
    const currentState = this.getCurrentState();
    const appImage = await this.getCurrentAppPicture(currentState);
    this.setCurrentState('mainAppImage', { mainAppImage: appImage.urls.full });
    const newState = this.getCurrentState();
    container.style.background = `linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(${newState.mainAppImage}) no-repeat`;
    container.style.backgroundSize = 'cover';
    setTimeout(() => {
      changeImage.classList.remove('rotateIn');
      changeImage.setAttribute('disabled', 'false');
    }, 3000);
  }

  async changeCurrentLanguageHandler(e) {
    const newLang = e.currentTarget.value;
    localStorage.setItem('language', newLang);
    const select = document.querySelector('#language');
    select.classList.add('tada');
    document.querySelector('#search-input').placeholder = `${getPlaceholderAndSearch[localStorage.getItem('language')].placeHolder}`;
    document.querySelector('.searc-btn').value = `${getPlaceholderAndSearch[localStorage.getItem('language')].search}`;
    const currentState = this.getCurrentState();
    const newLocationLang = await this.getCountryName(currentState.userLocation);
    currentState.userLocation.city = newLocationLang.results[0].components.city
      ? newLocationLang.results[0].components.city
      : newLocationLang.results[0].components.state;
    currentState.userLocation.country = newLocationLang.results[0].components.country;
    this.defineCurrentLocalTime();
    this.defineCurrentYearTime();
    const changeState = this.getCurrentState();
    this.setDataToDOM(changeState);
    this.setCurrentState('newState', changeState);
    select.classList.remove('tada');
  }

  convertToFarenheitHandler(e) {
    if (localStorage.getItem('isCelsius') === 'no') {
      return;
    }
    const animatedIcon = document.querySelector('.rubberBand');
    if (animatedIcon) {
      animatedIcon.classList.remove('rubberBand');
    }
    const farImage = document.querySelector('.forecast-controll--settings-btn-far');
    farImage.classList.add('rubberBand');
    const currentActiveBtn = document.querySelector('.activeDegreesBtn');
    currentActiveBtn.classList.remove('activeDegreesBtn');
    e.currentTarget.classList.add('activeDegreesBtn');
    localStorage.setItem('isCelcius', 'no');
    const currentState = this.getCurrentState();
    this.setDataToDOM(currentState);
  }

  convertToCelciustHandler(e) {
    if (localStorage.getItem('isCelsius') === 'yes') {
      return;
    }
    const animatedIcon = document.querySelector('.rubberBand');
    if (animatedIcon) {
      animatedIcon.classList.remove('rubberBand');
    }
    const celImage = document.querySelector('.forecast-controll--settings-btn-cel');
    celImage.classList.add('rubberBand');
    const currentActiveBtn = document.querySelector('.activeDegreesBtn');
    currentActiveBtn.classList.remove('activeDegreesBtn');
    e.currentTarget.classList.add('activeDegreesBtn');
    localStorage.setItem('isCelcius', 'yes');
    const currentState = this.getCurrentState();
    this.setDataToDOM(currentState);
  }
}
