import { getPlaceholderAndSearch } from '../../../constants/constants';
import SearchBarController from '../../controllers/SearchBarController/SearchBarController';
import './SearchBar.scss';

export default class SearchBar {
  constructor(
    state,
    getCurrentForecastHandler,
    getCountryName,
    getConvertCoords,
    setDataToDOM,
    setCurrentState,
    getCurrentState,
    defineCurrentLocalTime,
    defineCurrentMapLocation,
    defineCurrentWeather,
    defineCurrentYearTime,
    defineDayTime,
    getCurrentAppPicture,
  ) {
    this.state = state;
    this.getCurrentForecastHandler = getCurrentForecastHandler;
    this.getCountryName = getCountryName;
    this.getConvertCoords = getConvertCoords;
    this.setDataToDOM = setDataToDOM;
    this.setCurrentState = setCurrentState;
    this.getCurrentState = getCurrentState;
    this.defineCurrentLocalTime = defineCurrentLocalTime;
    this.defineCurrentMapLocation = defineCurrentMapLocation;
    this.defineCurrentWeather = defineCurrentWeather;
    this.defineCurrentYearTime = defineCurrentYearTime;
    this.defineDayTime = defineDayTime;
    this.getCurrentAppPicture = getCurrentAppPicture;
  }

  render() {
    const forecastSeacrbarWrapper = document.createElement('div');
    forecastSeacrbarWrapper.classList.add('forecast-controll--search-block');

    const searchBar = document.createElement('input');
    const searchBarPlaceholderValue = localStorage.getItem('language') ? `${getPlaceholderAndSearch[localStorage.getItem('language')].placeHolder}`
      : 'Enter your location';
    searchBar.id = 'search-input';
    searchBar.setAttribute('type', 'text');
    searchBar.setAttribute('placeholder', `${searchBarPlaceholderValue}`);

    const searchByVoice = document.createElement('div');
    searchByVoice.classList.add('voiceSearch');
    const microphone = document.createElement('i');
    microphone.classList.add('fas');
    microphone.classList.add('fa-microphone');

    const searchBarBtn = document.createElement('input');
    const searchBarValue = localStorage.getItem('language') ? `${getPlaceholderAndSearch[localStorage.getItem('language')].search}`
      : 'SEARCH';
    searchBarBtn.classList.add('searc-btn');
    searchBarBtn.setAttribute('value', `${searchBarValue}`);
    searchBarBtn.setAttribute('type', 'submit');

    forecastSeacrbarWrapper.appendChild(searchBar);
    forecastSeacrbarWrapper.appendChild(searchByVoice);
    searchByVoice.appendChild(microphone);
    forecastSeacrbarWrapper.appendChild(searchBarBtn);

    const searchBarController = new SearchBarController(
      this.state,
      this.getCurrentForecastHandler,
      this.getCountryName,
      this.getConvertCoords,
      this.setDataToDOM,
      this.setCurrentState,
      this.getCurrentState,
      this.defineCurrentLocalTime,
      this.defineCurrentMapLocation,
      this.defineCurrentWeather,
      this.defineCurrentYearTime,
      this.defineDayTime,
      this.getCurrentAppPicture,
    );
    searchBarController.addEventListenerHandler(searchBarBtn, searchByVoice);
    return forecastSeacrbarWrapper;
  }
}
