import { getFullMonthName, getWheatherConditions, getFullDayName } from '../../constants/constants';

export default class Controller {
  constructor() {
    this.state = {
      language: 'en',
      userLocation: {
        city: 'Minsk',
      },
      isCelcius: true,
    };
    this.getCurrentState = this.getCurrentState.bind(this);
    this.setCurrentState = this.setCurrentState.bind(this);
    this.defineCurrentLocalTime = this.defineCurrentLocalTime.bind(this);
    this.defineCurrentYearTime = this.defineCurrentYearTime.bind(this);
    this.defineCurrentWeather = this.defineCurrentWeather.bind(this);
    this.defineDayTime = this.defineDayTime.bind(this);
  }

  getCurrentState(stateProp) {
    return this.state[stateProp] ? this.state[stateProp] : this.state;
  }

  setCurrentState(stateProp, data) {
    if (this.state[stateProp] && typeof (this.state[stateProp]) === 'object') {
      this.state[stateProp] = { ...this.state[stateProp], ...data };
    } else {
      this.state = { ...this.state, ...data };
    }
  }

  defineCurrentLocalTime() {
    const timezone = this.state.timeZoneDelta;
    const currentDate = new Date();
    const utc = currentDate.getTime() + (currentDate.getTimezoneOffset() * 60000);
    let localDate = new Date(utc + (3600000 * timezone)).toString();
    const localDateArr = localDate.slice(0, -35).split(' ');
    localDateArr[0] = localStorage.getItem('language') ? getFullDayName[localDateArr[0]][localStorage.getItem('language')].short : localDateArr[0];
    const saveLocalDateOnEN = localDateArr[1];
    localDateArr[1] = localStorage.getItem('language') ? getFullMonthName[localDateArr[1]].fullname[localStorage.getItem('language')]
      : getFullMonthName[saveLocalDateOnEN].fullname.en;
    // const secondArrEl = localDateArr[2];
    // localDateArr[2] = localDateArr[1];
    [localDateArr[1], localDateArr[2]] = [localDateArr[2], localDateArr[1]];
    // localDateArr[1] = secondArrEl;
    localDateArr.splice(3, 1);
    localDate = localDateArr.join(' ');
    this.setCurrentState('localTime', { localTime: localDate });
    this.setCurrentState('localTimeOnENG', { saveLocalDateOnEN });
    const actualDate = document.querySelector('.forecast-info--current-date');
    if (!actualDate) {
      return;
    }
    actualDate.innerHTML = `${this.state.localTime}`;
  }

  defineCurrentYearTime() {
    const currentMonth = this.state.saveLocalDateOnEN;
    const currentYearTime = getFullMonthName[currentMonth].yearTime;
    this.setCurrentState('yearTime', { yearTime: currentYearTime });
  }

  defineCurrentWeather() {
    const {
      currentWeather: {
        weather,
      } = {},
    } = this.state;
    const currentWeatherCondition = getWheatherConditions[weather[0].description]
      ? getWheatherConditions[weather[0].description].en : getWheatherConditions.init;
    this.setCurrentState('weatherConditions', { weatherConditions: currentWeatherCondition });
  }

  defineDayTime() {
    const currentHours = Number(this.state.localTime.slice(16, 18));
    let dayTime;
    if (currentHours >= 5 && currentHours < 12) {
      dayTime = 'morning';
      this.setCurrentState('dayTime', { dayTime });
    }
    if (currentHours >= 12 && currentHours < 18) {
      this.setCurrentState('dayTime', { dayTime: 'day' });
    }
    if (currentHours >= 18 || currentHours < 5) {
      this.setCurrentState('dayTime', { dayTime: 'night' });
    }
  }

  // defineCurrentMapLocation() {
  //   const locationCoords  = this.state.userLocation.loc.split(',');
  //   mapboxgl.accessToken = getMapLocation;
  //   let map = new mapboxgl.Map({
  //     container: 'map',
  //     style: 'mapbox://styles/mapbox/streets-v11',
  //     center: [locationCoords[1], locationCoords[0]],
  //     zoom: 9,
  //   });
  //   return map;
  // }
}
