import {
  getUserCurrentLocation,
  getForecastFirst,
  getForecastSecond,
  getForecastThird,
  getApplicationImageFirstPart,
  getApplicationImageSecondPart,
  getConvertLocationCoordsFirstPart,
  getConvertLocationCoordsSecondPart,
  getCountryNameFirstPart,
  getCountryNameSecondPart,
} from '../../constants/constants';

export default class AppModel {
  constructor(setCurrentState) {
    this.setCurrentState = setCurrentState;
  }

  async getUserCurrentLocationHandler() {
    return fetch(getUserCurrentLocation)
      .then((res) => res.json());
  }

  async getCurrentForecastHandler(state) {
    const lat = Math.floor(state.userLocation.lat);
    const lon = Math.floor(state.userLocation.lng);
    const lang = 'EN';
    return fetch(`${getForecastFirst}lat=${lat}&lon=${lon}${getForecastSecond}${lang}${getForecastThird}`)
      .then((res) => {
        if (res.status !== 200) {
          throw new Error('Not 200!');
        } else {
          return res.json();
        }
      })
      .catch(() => this.setCurrentState('error', { error: true }));
  }

  async getCurrentAppPicture(state) {
    const { yearTime } = state;
    const { weatherConditions } = state;
    const { dayTime } = state;
    return fetch(`${getApplicationImageFirstPart}${yearTime},${dayTime},${weatherConditions}${getApplicationImageSecondPart}`)
      .then((res) => res.json());
  }

  async getConvertCoords(state) {
    const locationCoords = state.userLocation.loc.split(',');
    const lat = locationCoords[0];
    const lang = locationCoords[1];
    return fetch(`${getConvertLocationCoordsFirstPart}${lat}+${lang}${getConvertLocationCoordsSecondPart}`)
      .then((res) => res.json());
  }

  async getCountryName(state) {
    const currentCity = state.city;
    if (!currentCity) {
      this.setCurrentState('error', { error: true });
      return;
    }
    const lang = localStorage.getItem('language') ? localStorage.getItem('language') : 'en';
    return fetch(`${getCountryNameFirstPart}${currentCity}${getCountryNameSecondPart}&language=${lang}`)
      .then((res) => {
        if (res.status !== 200) {
          throw new Error();
        } else {
          return res.json();
        }
      })
      .then((data) => {
        if (!data.results.length) {
          throw new Error();
        } else {
          return data;
        }
      })
      .catch(() => {
        this.setCurrentState('error', { error: true });
      });
  }
}
