import { getMapLocation } from '../constants/constants';
import InitialApp from './views/InitialApp';
import AppModel from './models/AppModel';
import Controller from './controllers/Conroller';

import './styles/style.scss';

const mapboxgl = require('mapbox-gl/dist/mapbox-gl.js');

const controll = new Controller();
const data = new AppModel(controll.setCurrentState);

async function waitingUserLocation() {
  const userLocation = await data.getUserCurrentLocationHandler();
  const currentLocation = await data.getCountryName(userLocation);
  const currentCity = currentLocation.results[0].components.city
    ? currentLocation.results[0].components.city : currentLocation.results[0].components.state;
  const currentCountry = currentLocation.results[0].components.country;
  const { lat } = currentLocation.results[0].geometry;
  const { lng } = currentLocation.results[0].geometry;
  userLocation.city = currentCity;
  userLocation.country = currentCountry;
  userLocation.lat = lat;
  userLocation.lng = lng;
  controll.setCurrentState('userLocation', userLocation);
}

async function waitingCurrentForecast() {
  const currentForecast = await data.getCurrentForecastHandler(controll.state);
  const timeZoneDelta = currentForecast.city.timezone / 3600;
  controll.setCurrentState('timeZoneDelta', { timeZoneDelta });
  controll.setCurrentState('currentWeather', { currentWeather: currentForecast.list[0] });
  controll.setCurrentState('nextDayWeather', { nextDayWeather: currentForecast.list[8] });
  controll.setCurrentState('nextDayAfterWeather', { nextDayAfterWeather: currentForecast.list[16] });
  controll.setCurrentState('nextDayAfterNextWeather', { nextDayAfterNextWeather: currentForecast.list[24] });
}

async function waitingCurrentAppImage() {
  const appImage = await data.getCurrentAppPicture(controll.state);
  controll.setCurrentState('mainAppImage', { mainAppImage: appImage.urls.full });
}

async function waitingConvetCoords() {
  const convertCoords = await data.getConvertCoords(controll.state);
  const latitude = convertCoords.results[0].annotations.DMS.lat.slice(0, 7);
  const longitude = convertCoords.results[0].annotations.DMS.lng.slice(0, 7);
  controll.setCurrentState('latitude', { latitude });
  controll.setCurrentState('longitude', { longitude });
}

function defineCurrentMapLocation() {
  const locationCoords = controll.getCurrentState().userLocation.loc.split(',');
  mapboxgl.accessToken = getMapLocation;
  const map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/mapbox/streets-v11',
    center: [locationCoords[1], locationCoords[0]],
    zoom: 9,
  });
  return map;
}

window.onload = async () => {
  await waitingUserLocation();
  await waitingCurrentForecast();
  controll.defineCurrentLocalTime();
  controll.defineCurrentYearTime();
  controll.defineCurrentWeather();
  controll.defineDayTime();
  await waitingCurrentAppImage();
  await waitingConvetCoords();
  const app = new InitialApp(
    controll.state,
    data.getCurrentForecastHandler,
    data.getCountryName,
    data.getConvertCoords,
    controll.setCurrentState,
    controll.getCurrentState,
    controll.defineCurrentLocalTime,
    defineCurrentMapLocation,
    controll.defineCurrentWeather,
    controll.defineCurrentYearTime,
    controll.defineDayTime,
    data.getCurrentAppPicture,
  );
  app.render();
  defineCurrentMapLocation();
  setInterval(controll.defineCurrentLocalTime.bind(controll), 59000);
};
