import SearchBar from './SearchBar/SearchBar';
import ForecastSettings from './ForecastSettings/ForecastSettings';
import ForecastInfo from './ForecastInfo/ForecastInfo';
import SettingsBarController from '../controllers/SettingsBarController/SettingsBarController';
import ErrorModal from './ErrorModal/ErrorModal';

import './InitialApp.scss';

export default class InitialApp {
  constructor(
    state,
    getCurrentForecastHandler,
    getCountryName,
    getConvertCoords,
    setCurrentState,
    getCurrentState,
    defineCurrentLocalTime,
    defineCurrentMapLocation,
    defineCurrentWeather,
    defineCurrentYearTime,
    defineDayTime,
    getCurrentAppPicture,
  ) {
    this.state = state;
    this.getCurrentForecastHandler = getCurrentForecastHandler;
    this.getCountryName = getCountryName;
    this.getConvertCoords = getConvertCoords;
    this.setCurrentState = setCurrentState;
    this.getCurrentState = getCurrentState;
    this.defineCurrentLocalTime = defineCurrentLocalTime;
    this.defineCurrentMapLocation = defineCurrentMapLocation;
    this.defineCurrentWeather = defineCurrentWeather;
    this.defineCurrentYearTime = defineCurrentYearTime;
    this.defineDayTime = defineDayTime;
    this.getCurrentAppPicture = getCurrentAppPicture;
  }

  render() {
    const container = document.createElement('div');
    container.classList.add('container');

    const forecastControll = document.createElement('section');
    forecastControll.classList.add('forecast-controll');

    const forecastBlock = new ForecastInfo(this.state);

    const searchBar = new SearchBar(
      this.state,
      this.getCurrentForecastHandler,
      this.getCountryName,
      this.getConvertCoords,
      forecastBlock.setDataToDOM,
      this.setCurrentState,
      this.getCurrentState,
      this.defineCurrentLocalTime,
      this.defineCurrentMapLocation,
      this.defineCurrentWeather,
      this.defineCurrentYearTime,
      this.defineDayTime,
      this.getCurrentAppPicture,
    );
    const forecastSeacrbarWrapper = searchBar.render();
    const forecastBlockWeather = forecastBlock.render();

    const settings = new ForecastSettings(this.state, this.getCurrentState);
    const forecastSettings = settings.render();

    document.body.appendChild(container);
    container.appendChild(forecastControll);
    const error = new ErrorModal(this.getCurrentState, this.setCurrentState);
    const errorModal = error.render();
    container.appendChild(errorModal);
    forecastControll.appendChild(forecastSettings);
    forecastControll.appendChild(forecastSeacrbarWrapper);
    container.appendChild(forecastBlockWeather);
    forecastBlock.setDataToDOM(this.state);
    container.style.background = `linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(${this.state.mainAppImage}) no-repeat`;
    container.style.backgroundSize = 'cover';
    const settingsBarController = new SettingsBarController(
      this.state,
      this.getCurrentState,
      forecastBlock.setDataToDOM,
      this.getCountryName,
      this.setCurrentState,
      this.defineCurrentLocalTime,
      this.defineCurrentYearTime,
      this.defineDayTime,
      this.getCurrentAppPicture,
    );
    settingsBarController.addEventListenersHandler();
  }
}
