import {
  gettWeatherImageFirstPart,
  gettWeatherImageSecondPart,
  getFullDayName,
  getWheatherConditions,
  getWeatherDescription,
  getTranslateLatLong,
} from '../../../constants/constants';
import './ForecastInfo.scss';

export default class ForecastInfo {
  constructor(state) {
    this.state = state;
    this.setDataToDOM = this.setDataToDOM.bind(this);
    this.convertFromCelToFar = this.convertFromCelToFar.bind(this);
  }

  render() {
    this.forecastInfo = document.createElement('section');
    this.forecastInfo.classList.add('forecast-info');

    this.weatherForecastInfoWrapper = document.createElement('div');
    this.weatherForecastInfoWrapper.classList.add('forecast-info--weather-info');
    this.mapCurrentLocationWrapper = document.createElement('div');
    this.mapCurrentLocationWrapper.classList.add('forecast-info--map-location');

    this.currentLocation = document.createElement('div');
    this.currentLocation.classList.add('forecast-info--current-location');
    this.currentDate = document.createElement('div');
    this.currentDate.classList.add('forecast-info--current-date');
    this.currentTemperatureWrapper = document.createElement('div');
    this.currentTemperatureWrapper.classList.add('forecast-info--current-temperature-wrapper');
    this.nextDaysForecastWrapper = document.createElement('div');
    this.nextDaysForecastWrapper.classList.add('forecast-info--forecast-wrapper');

    this.currentCity = document.createElement('span');
    this.currentCity.classList.add('forecast-info--current-city');
    this.currentCountry = document.createElement('span');
    this.currentCountry.classList.add('forecast-info--current-contry');

    this.currentTemperature = document.createElement('div');
    this.currentTemperature.classList.add('forecast-info--current-temperature');
    this.currentTemperatureData = document.createElement('span');
    this.currentTemperatureData.classList.add('forecast-info--current-temperature-data');

    this.currentTemperatureImg = document.createElement('div');
    this.currentTemperatureImg.classList.add('forecast-info--current-temperature-img');
    this.temperatureIcon = document.createElement('img');
    this.temperatureIcon.classList.add('forecast-info--temperature-icon');

    this.currentTemperatureInfo = document.createElement('div');
    this.currentTemperatureInfo.classList.add('forecast-info--temperature-info');
    this.cloudy = document.createElement('span');
    this.cloudy.classList.add('forecast-info--cloudy');
    this.pressureDate = document.createElement('span');
    this.pressureDate.classList.add('forecast-info--pressureDate');
    this.windDate = document.createElement('span');
    this.windDate.classList.add('forecast-info--windDate');
    this.humidityDate = document.createElement('span');
    this.humidityDate.classList.add('forecast-info--humidityDate');

    this.firstDayForecast = document.createElement('div');
    this.firstDayForecast.classList.add('forecast-info--forecast-firstday');
    this.firstDayForecast.classList.add('forecast-info--forecast');
    this.firstDayForecastName = document.createElement('p');
    this.firstDayForecastName.classList.add('forecast-info--firstday-name');
    this.firstDayForecastInfo = document.createElement('div');
    this.firstDayForecastInfo.classList.add('forecast-info--firstday-info');
    this.firstDayForecastTemperature = document.createElement('div');
    this.firstDayForecastTemperature.classList.add('forecast-info--firstday-temp');
    this.firstDayForecastTemperature.classList.add('forecast-info--temp');
    this.firstDayForecastImgBlock = document.createElement('div');
    this.firstDayForecastImgBlock.classList.add('forecast-info--firstday-image');
    this.firstDayForecastImgBlock.classList.add('forecast-info--image');
    this.firstDayForecastIcon = document.createElement('img');
    this.firstDayForecastIcon.classList.add('forecast-info--firstday-icon');

    this.secondDayForecast = document.createElement('div');
    this.secondDayForecast.classList.add('forecast-info--forecast-secondday');
    this.secondDayForecast.classList.add('forecast-info--forecast');
    this.secondDayForecastName = document.createElement('p');
    this.secondDayForecastName.classList.add('forecast-info--secondday-name');
    this.secondDayForecastInfo = document.createElement('div');
    this.secondDayForecastInfo.classList.add('forecast-info--secondday-info');
    this.secondDayForecastTemperature = document.createElement('div');
    this.secondDayForecastTemperature.classList.add('forecast-info--secondday-temp');
    this.secondDayForecastTemperature.classList.add('forecast-info--temp');
    this.secondDayForecastImgBlock = document.createElement('div');
    this.secondDayForecastImgBlock.classList.add('forecast-info--secondday-image');
    this.secondDayForecastImgBlock.classList.add('forecast-info--image');
    this.secondDayForecastIcon = document.createElement('img');
    this.secondDayForecastIcon.classList.add('forecast-info--secondday-icon');

    this.thirdDayForecast = document.createElement('div');
    this.thirdDayForecast.classList.add('forecast-info--forecast-thirdday');
    this.thirdDayForecast.classList.add('forecast-info--forecast');
    this.thirdDayForecastName = document.createElement('p');
    this.thirdDayForecastName.classList.add('forecast-info--thirdday-name');
    this.thirdDayForecastInfo = document.createElement('div');
    this.thirdDayForecastInfo.classList.add('forecast-info--thirdday-info');
    this.thirdDayForecastTemperature = document.createElement('div');
    this.thirdDayForecastTemperature.classList.add('forecast-info--thirdday-temp');
    this.thirdDayForecastTemperature.classList.add('forecast-info--temp');
    this.thirdDayForecastImgBlock = document.createElement('div');
    this.thirdDayForecastImgBlock.classList.add('forecast-info--thirdday-image');
    this.thirdDayForecastImgBlock.classList.add('forecast-info--image');
    this.thirdDayForecastIcon = document.createElement('img');
    this.thirdDayForecastIcon.classList.add('forecast-info--thirdday-icon');

    this.mapBoxContainer = document.createElement('div');
    this.mapBoxContainer.setAttribute('id', 'map');

    this.coordinatesBlock = document.createElement('div');
    this.coordinatesBlock.classList.add('forecast-info--coords');
    this.latitudeBlock = document.createElement('p');
    this.latitudeBlock.classList.add('forecast-info--latitude');
    this.longitudeBlock = document.createElement('p');
    this.longitudeBlock.classList.add('forecast-info--longitude');

    this.forecastInfo.appendChild(this.weatherForecastInfoWrapper);
    this.forecastInfo.appendChild(this.mapCurrentLocationWrapper);
    this.weatherForecastInfoWrapper.appendChild(this.currentLocation);
    this.weatherForecastInfoWrapper.appendChild(this.currentTemperatureWrapper);
    this.weatherForecastInfoWrapper.appendChild(this.nextDaysForecastWrapper);
    this.mapCurrentLocationWrapper.appendChild(this.mapBoxContainer);
    this.mapCurrentLocationWrapper.appendChild(this.coordinatesBlock);

    this.coordinatesBlock.appendChild(this.latitudeBlock);
    this.coordinatesBlock.appendChild(this.longitudeBlock);

    this.currentLocation.appendChild(this.currentCity);
    this.currentLocation.appendChild(this.currentCountry);
    this.currentLocation.appendChild(this.currentDate);

    this.currentTemperatureWrapper.appendChild(this.currentTemperature);
    this.currentTemperature.appendChild(this.currentTemperatureData);
    this.currentTemperature.appendChild(this.currentTemperatureImg);
    this.currentTemperatureWrapper.appendChild(this.currentTemperatureInfo);

    this.currentTemperatureImg.appendChild(this.temperatureIcon);

    this.currentTemperatureInfo.appendChild(this.cloudy);
    this.currentTemperatureInfo.appendChild(this.pressureDate);
    this.currentTemperatureInfo.appendChild(this.windDate);
    this.currentTemperatureInfo.appendChild(this.humidityDate);

    this.nextDaysForecastWrapper.appendChild(this.firstDayForecast);
    this.nextDaysForecastWrapper.appendChild(this.secondDayForecast);
    this.nextDaysForecastWrapper.appendChild(this.thirdDayForecast);

    this.firstDayForecast.appendChild(this.firstDayForecastName);
    this.firstDayForecast.appendChild(this.firstDayForecastInfo);

    this.firstDayForecastInfo.appendChild(this.firstDayForecastTemperature);
    this.firstDayForecastInfo.appendChild(this.firstDayForecastImgBlock);
    this.firstDayForecastImgBlock.appendChild(this.firstDayForecastIcon);

    this.secondDayForecast.appendChild(this.secondDayForecastName);
    this.secondDayForecast.appendChild(this.secondDayForecastInfo);

    this.secondDayForecastInfo.appendChild(this.secondDayForecastTemperature);
    this.secondDayForecastInfo.appendChild(this.secondDayForecastImgBlock);
    this.secondDayForecastImgBlock.appendChild(this.secondDayForecastIcon);

    this.thirdDayForecast.appendChild(this.thirdDayForecastName);
    this.thirdDayForecast.appendChild(this.thirdDayForecastInfo);

    this.thirdDayForecastInfo.appendChild(this.thirdDayForecastTemperature);
    this.thirdDayForecastInfo.appendChild(this.thirdDayForecastImgBlock);
    this.thirdDayForecastImgBlock.appendChild(this.thirdDayForecastIcon);

    return this.forecastInfo;
  }

  setDataToDOM(state) {
    const {
      localTime,
      longitude,
      latitude,
      userLocation: { city, country } = {},
      currentWeather:
          {
            main: { temp, humidity, pressure } = {},
            weather,
            wind: { speed } = {},
          } = {},
      nextDayWeather:
          {
            dt_txt: firstDayDx,
            main: { temp: firstDayTemp } = {},
            weather: firstDayWeather,
          } = {},
      nextDayAfterWeather:
        {
          dt_txt: secondDayDx,
          main: { temp: secondDayTemp } = {},
          weather: secondDayWeather,
        } = {},
      nextDayAfterNextWeather:
        {
          dt_txt: thirdDayDx,
          main: { temp: thirdDayTemp } = {},
          weather: thirdDayWeather,
        } = {},
    } = state;

    const currentCity = document.querySelector('.forecast-info--current-city');
    currentCity.innerHTML = `${city.toUpperCase()}, `;

    const currentCountry = document.querySelector('.forecast-info--current-contry');
    currentCountry.innerHTML = `${country.toUpperCase()}`;

    const currentDate = document.querySelector('.forecast-info--current-date');
    currentDate.innerHTML = `${localTime}`;

    const currentTemperatureData = document.querySelector('.forecast-info--current-temperature-data');
    if (localStorage.getItem('isCelcius') === 'no') {
      const tempInFar = this.convertFromCelToFar(temp);
      currentTemperatureData.innerHTML = `${Math.round(tempInFar)}`;
    } else {
      currentTemperatureData.innerHTML = `${Math.round(temp)}`;
    }

    const cloudy = document.querySelector('.forecast-info--cloudy');
    if (localStorage.getItem('language') && getWheatherConditions[weather[0].description]) {
      cloudy.innerHTML = `${getWheatherConditions[weather[0].description][localStorage.getItem('language')].toUpperCase()}`;
    } else if (localStorage.getItem('language') && !getWheatherConditions[weather[0].description]) {
      cloudy.innerHTML = getWheatherConditions.init[localStorage.getItem('language')].toUpperCase();
    } else {
      cloudy.innerHTML = getWheatherConditions[weather[0].description].initial.toUpperCase();
    }

    const pressureDate = document.querySelector('.forecast-info--pressureDate');
    pressureDate.innerHTML = localStorage.getItem('language')
      ? `${getWeatherDescription.pressure[localStorage.getItem('language')].toUpperCase()}: ${Math.round(pressure)}` : `PRESSURE: ${Math.round(pressure)}`;

    const windDate = document.querySelector('.forecast-info--windDate');
    windDate.innerHTML = localStorage.getItem('language')
      ? `${getWeatherDescription.wind[localStorage.getItem('language')].desc.toUpperCase()}: ${Math.round(speed)} ${getWeatherDescription.wind[localStorage.getItem('language')].num}`
      : `WIND: ${Math.round(speed)} m/s`;

    const humidityDate = document.querySelector('.forecast-info--humidityDate');
    humidityDate.innerHTML = localStorage.getItem('language')
      ? `${getWeatherDescription.humidity[localStorage.getItem('language')].toUpperCase()}: ${Math.round(humidity)}%` : `HUMIDITY: ${Math.round(humidity)} %`;

    const temperatureIcon = document.querySelector('.forecast-info--temperature-icon');
    temperatureIcon.setAttribute('src', `${gettWeatherImageFirstPart}${weather[0].icon}${gettWeatherImageSecondPart}`);

    const newDateFirst = new Date(Date.parse(firstDayDx));
    const firstDayForecastName = document.querySelector('.forecast-info--firstday-name');
    firstDayForecastName.innerHTML = localStorage.getItem('language')
      ? `${getFullDayName[newDateFirst.toDateString().slice(0, 3)][localStorage.getItem('language')].full}` : `${getFullDayName[newDateFirst.toDateString().slice(0, 3)].en.full}`;
    const firstDayForecastTemperature = document.querySelector('.forecast-info--firstday-temp');
    if (localStorage.getItem('isCelcius') === 'no') {
      const tempInFarFirst = this.convertFromCelToFar(firstDayTemp);
      firstDayForecastTemperature.innerHTML = `${Math.round(tempInFarFirst)}`;
    } else {
      firstDayForecastTemperature.innerHTML = `${Math.floor(firstDayTemp)}`;
    }
    const firstDayForecastIcon = document.querySelector('.forecast-info--firstday-icon');
    firstDayForecastIcon.setAttribute('src', `${gettWeatherImageFirstPart}${firstDayWeather[0].icon}${gettWeatherImageSecondPart}`);

    const newDateSecond = new Date(Date.parse(secondDayDx));
    const secondDayForecastName = document.querySelector('.forecast-info--secondday-name');
    secondDayForecastName.innerHTML = localStorage.getItem('language')
      ? `${getFullDayName[newDateSecond.toDateString().slice(0, 3)][localStorage.getItem('language')].full}` : `${getFullDayName[newDateSecond.toDateString().slice(0, 3)].en.full}`;
    const secondDayForecastTemperature = document.querySelector('.forecast-info--secondday-temp');
    if (localStorage.getItem('isCelcius') === 'no') {
      const tempInFarSecond = this.convertFromCelToFar(secondDayTemp);
      secondDayForecastTemperature.innerHTML = `${Math.round(tempInFarSecond)}`;
    } else {
      secondDayForecastTemperature.innerHTML = `${Math.floor(secondDayTemp)}`;
    }

    const secondDayForecastIcon = document.querySelector('.forecast-info--secondday-icon');
    secondDayForecastIcon.setAttribute('src', `${gettWeatherImageFirstPart}${secondDayWeather[0].icon}${gettWeatherImageSecondPart}`);

    const newDateThird = new Date(Date.parse(thirdDayDx));
    const thirdDayForecastName = document.querySelector('.forecast-info--thirdday-name');
    thirdDayForecastName.innerHTML = localStorage.getItem('language')
      ? `${getFullDayName[newDateThird.toDateString().slice(0, 3)][localStorage.getItem('language')].full}` : `${getFullDayName[newDateThird.toDateString().slice(0, 3)].en.full}`;
    const thirdDayForecastTemperature = document.querySelector('.forecast-info--thirdday-temp');
    if (localStorage.getItem('isCelcius') === 'no') {
      const tempInFarThird = this.convertFromCelToFar(thirdDayTemp);
      thirdDayForecastTemperature.innerHTML = `${Math.round(tempInFarThird)}`;
    } else {
      thirdDayForecastTemperature.innerHTML = `${Math.floor(thirdDayTemp)}`;
    }
    const thirdDayForecastIcon = document.querySelector('.forecast-info--thirdday-icon');
    thirdDayForecastIcon.setAttribute('src', `${gettWeatherImageFirstPart}${thirdDayWeather[0].icon}${gettWeatherImageSecondPart}`);

    const latitudeBlock = document.querySelector('.forecast-info--latitude');
    latitudeBlock.innerHTML = localStorage.getItem('language') ? `${getTranslateLatLong[localStorage.getItem('language')].lat}: ${latitude}`
      : `${getTranslateLatLong.en.lat}: ${latitude}`;
    const longitudeBlock = document.querySelector('.forecast-info--longitude');
    longitudeBlock.innerHTML = localStorage.getItem('language') ? `${getTranslateLatLong[localStorage.getItem('language')].long}: ${longitude}`
      : `${getTranslateLatLong.en.long}: ${longitude}`;
  }

  // eslint-disable-next-line class-methods-use-this
  convertFromCelToFar(deg) {
    return ((deg * 9) / 5) + 32;
  }
}
