import { getCurrentMessage } from '../../../constants/constants';
import './ErrorModal.scss';


export default class ErrorModal {
  constructor(getCurrentState, setCurrentState) {
    this.getCurrentState = getCurrentState;
    this.setCurrentState = setCurrentState;
    this.confirmErrorHandler = this.confirmErrorHandler.bind(this);
  }

  render() {
    const errorSection = document.createElement('div');
    errorSection.classList.add('error-block');

    const errorModal = document.createElement('div');
    errorModal.classList.add('error-block--modal');

    const errorMessage = document.createElement('p');
    errorMessage.classList.add('error-block--message');
    errorMessage.innerHTML = localStorage.getItem('language') ? getCurrentMessage[localStorage.getItem('language')].noResults
      : getCurrentMessage.en.noResults;

    const confirmBtn = document.createElement('button');
    confirmBtn.classList.add('error-block--button');
    confirmBtn.innerHTML = 'OK';
    confirmBtn.addEventListener('click', this.confirmErrorHandler);

    errorSection.appendChild(errorModal);
    errorModal.appendChild(errorMessage);
    errorModal.appendChild(confirmBtn);
    return errorSection;
  }

  async confirmErrorHandler() {
    const errorModal = document.querySelector('.error-block--modal');
    const errorBlock = document.querySelector('.error-block');
    errorModal.classList.add('zoomOut');
    setTimeout(() => {
      errorBlock.style.display = 'none';
      errorModal.classList.remove('zoomOut');
    }, 900);
    this.setCurrentState('error', { error: false });
  }
}
