/* eslint-disable no-undef */
/* eslint-disable new-cap */
import { getCurrentMessage } from '../../../constants/constants';

export default class SearchBarController {
  constructor(
    state,
    getCurrentForecastHandler,
    getCountryName,
    getConvertCoords,
    setDataToDOM,
    setCurrentState,
    getCurrentState,
    defineCurrentLocalTime,
    defineCurrentMapLocation,
    defineCurrentWeather,
    defineCurrentYearTime,
    defineDayTime,
    getCurrentAppPicture,
  ) {
    this.state = state;
    this.getCurrentForecastHandler = getCurrentForecastHandler;
    this.getCountryName = getCountryName;
    this.getConvertCoords = getConvertCoords;
    this.setDataToDOM = setDataToDOM;
    this.setCurrentState = setCurrentState;
    this.getCurrentState = getCurrentState;
    this.defineCurrentLocalTime = defineCurrentLocalTime;
    this.defineCurrentMapLocation = defineCurrentMapLocation;
    this.defineCurrentWeather = defineCurrentWeather;
    this.defineCurrentYearTime = defineCurrentYearTime;
    this.defineDayTime = defineDayTime;
    this.getCurrentAppPicture = getCurrentAppPicture;
  }

  addEventListenerHandler(searchButton, searchByVoice) {
    searchButton.addEventListener('click', () => this.getWeatherForInsertLocation(this.state));
    searchByVoice.addEventListener('click', this.speechRecognitionHandler);
    document.addEventListener('keypress', (e) => {
      if (e.code === 'Enter') {
        this.getWeatherForInsertLocation(this.state);
      }
    });
  }

  speechRecognitionHandler() {
    const recognition = new webkitSpeechRecognition();

    if (!localStorage.getItem('language')) {
      recognition.lang = 'en-EN';
    } else if (localStorage.getItem('language') === 'be') {
      recognition.lang = 'ru-RU';
    } else {
      recognition.lang = localStorage.getItem('language');
    }

    recognition.start();

    recognition.onstart = () => {
      const voiceEnterBtn = document.querySelector('.voiceSearch');
      voiceEnterBtn.classList.add('heartBeat');
    };

    recognition.onresult = (e) => {
      const searchInput = document.querySelector('#search-input');
      const voiceEnterBtn = document.querySelector('.voiceSearch');
      const res = e.results[0][0].transcript;
      searchInput.value = res;
      voiceEnterBtn.classList.remove('heartBeat');
    };

    recognition.onaudioend = () => {
      const voiceEnterBtn = document.querySelector('.voiceSearch');
      voiceEnterBtn.classList.remove('heartBeat');
    };
  }

  async getWeatherForInsertLocation(state) {
    const searchInput = document.querySelector('#search-input');
    const searchBtn = document.querySelector('.searc-btn');
    const container = document.querySelector('.container');
    searchBtn.classList.add('pulse');
    const inputValue = searchInput.value;
    const obj = {
      city: inputValue,
    };

    const currentState = { ...state };
    const currentCountryObj = await this.getCountryName(obj);
    const { city, state: town } = currentCountryObj ? currentCountryObj.results[0].components : {};
    const getState = this.getCurrentState();
    if (getState.error || (!city && !town)) {
      const errorModal = document.querySelector('.error-block');
      const errorMessage = document.querySelector('.error-block--message');
      errorMessage.innerHTML = localStorage.getItem('language') ? getCurrentMessage[localStorage.getItem('language')].noResults
        : getCurrentMessage.en.noResults;
      errorModal.style.display = 'block';
      return;
    }
    const currentCity = currentCountryObj.results[0].components.city
      ? currentCountryObj.results[0].components.city
      : currentCountryObj.results[0].components.state;
    const currentCountry = currentCountryObj.results[0].components.country;

    const currentLocationLat = currentCountryObj.results[0].geometry.lat;
    const currentLocationLng = currentCountryObj.results[0].geometry.lng;

    currentState.userLocation.city = currentCity;
    currentState.userLocation.country = currentCountry;
    currentState.userLocation.lat = currentLocationLat;
    currentState.userLocation.lng = currentLocationLng;
    currentState.userLocation.loc = `${currentLocationLat},${currentLocationLng}`;

    const forecastResult = await this.getCurrentForecastHandler(currentState);

    const timeZoneDelta = forecastResult.city.timezone / 3600;

    this.setCurrentState('newState', { ...currentState });
    this.setCurrentState('timeZoneDelta', { timeZoneDelta });
    this.setCurrentState('currentWeather', forecastResult.list[0]);
    this.setCurrentState('nextDayWeather', forecastResult.list[8]);
    this.setCurrentState('nextDayAfterWeather', forecastResult.list[16]);
    this.setCurrentState('nextDayAfterNextWeather', forecastResult.list[24]);
    this.defineCurrentLocalTime();
    this.defineCurrentYearTime();
    this.defineCurrentWeather();
    this.defineDayTime();
    this.defineCurrentMapLocation();
    const convertCoords = await this.getConvertCoords(currentState);
    const latitude = convertCoords.results[0].annotations.DMS.lat.slice(0, 7);
    const longitude = convertCoords.results[0].annotations.DMS.lng.slice(0, 7);
    this.setCurrentState('latitude', { latitude });
    this.setCurrentState('longitude', { longitude });

    const getCurrentState = this.getCurrentState();
    this.setDataToDOM(getCurrentState);
    const appImage = await this.getCurrentAppPicture(getCurrentState);
    this.setCurrentState('mainAppImage', { mainAppImage: appImage.urls.full });
    const newState = this.getCurrentState();
    container.style.background = `linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(${newState.mainAppImage}) no-repeat`;
    container.style.backgroundSize = 'cover';
    searchBtn.classList.remove('pulse');
  }
}
